import React from 'react';
import { useHistory } from 'react-router';

const Error = () => {
    const histroy = useHistory();
    return (
        <>
            <h1>404 Error Page!!!</h1>
            <p>Sorry, this page does't exit</p>
            <button
                onClick={() =>
                    histroy.push('/')
                }>
                Go Home
            </button>
        </>
    )
}

export default Error;
