import React from 'react';
import { Link } from 'react-router-dom';

const Home = (props) => {
    return (
        <>
            <div className="container">
                <h1 className="text-center my-5">Welcome to my {props.name} page</h1>
                <div className="row">
                    <div className="col-sm-4 g-3">
                        <div className="card" >
                            <img src="https://source.unsplash.com/200x150/?mango" className="card-img-top" alt="mango" />
                            <div className="card-body">
                                <h5 className="card-title">Card title</h5>
                                <p className="card-text">Some quick example text to build on the card title and make up the bulk of the card's content.</p>
                                <a href="https://reactrouter.com/web/api/Link" className="btn btn-outline-success">Go somewhere</a>
                            </div>
                        </div>
                    </div>
                    <div className="col-sm-4 g-3">
                        <div className="card" >
                            <img src="https://source.unsplash.com/200x150/?banana" className="card-img-top" alt="mango" />
                            <div className="card-body">
                                <h5 className="card-title">Card title</h5>
                                <p className="card-text">Some quick example text to build on the card title and make up the bulk of the card's content.</p>
                                <a href="https://reactrouter.com/web/api/Link" className="btn btn-outline-success">Go somewhere</a>
                            </div>
                        </div>
                    </div>
                    <div className="col-sm-4 g-3">
                        <div className="card" >
                            <img src="https://source.unsplash.com/200x150/?apple" className="card-img-top" alt="mango" />
                            <div className="card-body">
                                <h5 className="card-title">Card title</h5>
                                <p className="card-text">Some quick example text to build on the card title and make up the bulk of the card's content.</p>
                                <a href="https://reactrouter.com/web/api/Link" className="btn btn-outline-success">Go somewhere</a>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}

export default Home;
